.App {
  position: fixed;
  background-color: #eafbf7;
  width: 100%;
  height: 100vh;
  font-family: 'Slabo 27px', serif;
  overflow: scroll;
}

button {
  height: 26px;
  width: 50px;
  background-color: #5d5147ff;
  color: #EAFBF7;
  border-radius: 10%;
}
a {
  cursor: pointer;
  color: #EAFBF7;
  text-decoration: none;
}
